@import url("https://fonts.googleapis.com/css2?family=Charmonman:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.header {
  position: sticky;
  height: 75px;
  width: 100%;
  background-color: #1f1f1f;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid;
  border-image: linear-gradient(to right, #01a4e9 0%, white 100%) 1;
}

.logo-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

#header-logo {
  padding: 10px;
  height: -webkit-fill-available;
  width: auto;
}
@media screen and (max-device-width: 768px) {
  .header {
    height: 58px;
    // width: max-content;
  }
  .settings-button-container {
    width: fit-content;
    margin-right: 0px !important;
  }
  .user-details-container {
    margin: 0px !important;
  }
}
.header-right-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

#header-logo-text {
  font-family: "Charmonman", cursive;
  line-height: 2;
}

.user-details-container {
  margin: 0px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.settings-button-container {
  width: fit-content;
  margin-right: 10px;
}
